.where{
    height: calc(100vh - 80px + 100px);
    padding-top: 150px;
    position: relative;
    overflow: hidden;
    margin-top: -1px;
    //background-color: white;
    display: flex;
    justify-content: center;
    padding-bottom: 100px;


    @include mobileMax952p{
        //margin-top: -100px;
        padding-top: 0px;
        //padding-bottom: 100px;
        margin-bottom: -150px;
    }

    &__contentWrap{ 
        height: calc(100% - 350px);
        position: absolute;
        top: 350px;
        @include mobileMax952p{
            //height: calc(100% - 150px);
            //top: 150px;
            height: 100%;
            top: 0px;
        }
    }

    &__circleWrap{
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        &__circle{
            position: absolute;
            width: 1330px;
            height: 1330px;
            z-index: -1;
            background-color: transparent;
            border-radius: 100%;
            box-shadow: 0px 0px 0px 3000px var(--white-color);
            @include mobileMax952p{
                //width: 700px;
                //height: 700px;
                width: 190px;
                height: 190px;
            }
        }
        &__innercircle{
            position: absolute;
            z-index: -1;
            width: 1013px;
            height: 1013px;
            background-color: var(--white-color);
            border-radius: 100%;
            @include mobileMax952p{
                //width: 650px;
                //height: 650px;
                width: 180px;
                height: 180px;
            }
        }
    }
    
    .where__reveal{
        display: flex;
        height: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        a{
            text-decoration: none;
            &:link, &:visited,&:active{
                text-decoration: none;
            }
        }
        &__first{
            transform: translateY(80px);
            max-width: 907px;
            text-align: center;
            padding-bottom: 91px;
            opacity: 0;
            transition: opacity 0.3s ease-out;
            width: 907px;
            height: 300px;
            position: relative;
            @include windowMax1440p{
                padding-bottom: 80px;
                height: 264px;
            }
            @include mobileMax952p {
                padding-bottom: 110px;
                height: 144px;
                width: 314px;
            }
            p{
                @include dmsans92();
                @include windowMax1440p{
                    font-size: 80px;
                    line-height: 88px;
                }
                @include mobileMax952p{
                    font-size: 40px;
                    line-height: 48px;
                    br{
                        display: none;
                    }
                }
            }
            span{
                @include dmsans92_500();
                @include gradient2();
                @include windowMax1440p{
                    font-size: 80px;
                    line-height: 88px;
                }
                @include mobileMax952p{
                    font-size: 40px;
                    line-height: 48px;
                }
            }
        }
        &__second{
            display: flex;
            //transform: translateY(80px);
            opacity: 0;
            transition: opacity 0.3s ease-out;
            @include mobileMax952p{
                flex-direction: column;
                gap: 12px;
            }
            a{
                background-image: url("./images/bg/btn-bg.webp");
            }
            .box{
                transition: .7s ease-out;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 320px;
                height: 96px;
                background: #ffffffe0;
                color: var(--black-color);
                @include windowMax1440p{
                    width: 278px;
                }
                @include mobileMax952p{
                    width: 222px;
                    height: 64px;
                }
                p{
                    mix-blend-mode: none;
                    @include dmsans24();
                    @include windowMax1440p{
                        font-size: 20px;
                        line-height: 28px;
                    }
                    @include mobileMax952p{
                        font-size: 20px;
                        line-height: 52px;
                    }
                }
            }
            .active{
                background: none;
                color:var(--white-color);
            }
        }
    }
}