.nav__left {
  max-width: calc((100vw - 1330px) / 2);
  min-width: 216px;
  width: 100%;
  height: 100%;

  @include mobileMax952p {
    min-width: 160px;
  }
}

.nav__middle {
  max-width: 1330px;
  width: 100%;
  display: flex;
}

.nav__right {
  max-width: calc((100vw - 1330px) / 2);
  min-width: 216px;
  width: 100%;
  height: 100%;
}

.sb__nav__left {
  background-color: var(--colorLeft);
}

.sb__nav__right {
  background-color: var(--colorRight);
}

.mobileOnly {
  display: none;

  @include mobileMax952p {
    display: block;
  }
}

.fixed {
  position: fixed;
  width: 100%;
  max-height: 80px;
  overflow: hidden;
  --afterTop: 0;
  --afterHeight: 100%;

  @include windowMax1440p {
    max-height: 74px;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 100000;
    width: 100%;
    height: var(--afterHeight);
    top: var(--afterTop);
    background-color: #ffffff;
  }

  .nav {
    height: 79px;
    width: 100%;
    background-color: transparent;
    position: relative;
    display: flex;
    border-bottom: 1px solid var(--border-color);
    color: white;
    z-index: 99;
    
    @include windowMax1440p {
      max-height: 73px;
    }

    &__menu {
      display: flex;
      width: 100%;
      max-width: calc(1330px + (100vw - 1330px) / 2);
    }

    &__first {
      width: 100%;
      max-width: calc((100vw - 1330px) / 2);
      min-width: 216px;
    }

    .nav__burger {
      display: none;
      pointer-events: all;

      @include mobileMax952p {
        cursor: pointer;

        &::before {
          content: "";
          height: 2px;
          background-color: white;
          width: 18px;
          display: block;
          transition: 0.3s ease-in-out;
          transform-origin: 5px;
        }

        &::after {
          content: "";
          height: 2px;
          background-color: white;
          width: 18px;
          margin-top: 4px;
          display: block;
          transition: 0.3s ease-in-out;
          transform-origin: 5px;
        }
      }
    }

    .logo {
      display: flex;
      justify-content: center;
      align-items: center;

      a {
        display: flex;
        justify-content: center;
      }

      img {
        width: 126px;
        height: 35px;
        cursor: pointer;
      }
    }

    .container_nav {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-left: 1px solid var(--border-color);
      border-right: 1px solid var(--border-color);

      .language_switcher {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 30px;
        width: 74px;
        height: 40px;
        border: 1px solid var(--border-color);
        border-radius: 500px;
        cursor: pointer;
        position: relative;

        button {
          width: 32px;
          height: 32px;
          border-radius: 500px;
          color: var(--white-color);
          z-index: 1;
          transition: 0.3s color;
          cursor: pointer;
          @include dmsans12_500();
        }

        .active {
          color: #341A0C;
        }

        .circle {
          position: absolute;
          border-radius: 100%;
          width: 32px;
          height: 32px;
          background-color: var(--white-color);
          color: #341A0C;
          //margin-right: 32px;
          left: 5px;
          cursor: pointer;
          //transform: translateX(-50%);
          transition: 0.3s transform;

          &.clicked {
            transform: translateX(100%);
          }
        }
      }

      .services_nav {
        padding-right: 64px;
        display: flex;
        align-items: center;
        cursor: pointer;
        @include ibmplex();

        @include windowMax1440p {
          @include ibmplex14();
        }

        @include mobileMax952p {
          display: none;
        }

        p {
          margin-left: 16px;
        }
      }

    }

    .cta {
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      cursor: pointer;
      background: rgba($color: #000000, $alpha: 0.4);

      @include ibmplex();

      @include windowMax1440p {
        @include ibmplex14();
      }

      transition: 0.3s background-color;

      a {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        &:link,
        &:visited,
        &:active {
          text-decoration: none;
          color: var(--cultured_white-color);
        }
      }

      .nav__language-wrap {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      
      @media (hover: hover) {
        &:hover {
          background-color: rgba($color: #000000, $alpha: 0.8);
        }
      }
    }
  }

  .nav-language-wrap {
    width: 65px;
    height: 21px;
  }

  .service_menu_fixed {
    width: 100%;
    height: 119px;
    display: flex;
    justify-content: center;
    border-bottom: 1px solid var(--border-color);

    @include windowMax1440p {
      max-height: 74px;
    }

    @include mobileMax952p {
      display: none;
    }

    cursor: pointer;

    .service_box_fixed {
      transition: .5s ease-out, color .5s ease-out;
      width: 50%;
      height: 100%;
      color: white;
      display: flex;
      align-items: center;
      position: relative;
      justify-content: center;

      .service_header_fixed {
        display: inline;
      }
    }

    .text {
      font-size: 32px;

      @include windowMax1440p {
        font-size: 26.664px;
      }

      p {
        @include dmsans48_500();
        font-size: 1em;
      }

      span {
        @include dmsans48();
        font-size: 1em;
      }
    }

    .right {
      display: flex;

      .text {
        //padding-left: 64px;
      }
    }


    .notactive {
      background: #ffffffe0;

      //border-bottom: 1px solid #ffffffe0;
      .text {
        color: var(--black-color);
      }
    }
  }


  @include mobileMax952p {
    max-height: none;
    width: 100vw;
    overflow: hidden;
    height: 100vh;
    height: 100svh;
    h1 {
      @include dmsans16_500();
      color: rgba(255, 255, 255, 0.5);
    }

    p {
      @include dmsans14();
      line-height: 32px;
      color: #F5F5F5;
    }

    a {
      position: relative;
      display: table;

      &:link,
      &:visited,
      &:active {
        text-decoration: none;
        color: var(--cultured_white-color);
      }
    }


    .nav__language-wrap {
      min-width: 112px;
      height: 21px;
      position: relative;
    }

    .nav {
      display: flex;
      flex-direction: column;
      height: 100%;

      &__first {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 80px;
        min-height: 80px;
        max-width: none;
        border-bottom: 1px solid var(--border-color);

        &__burger {
          display: block;
          pointer-events: all;
          padding-right: 24px;

          
            cursor: pointer;

            .line {
              height: 1px;
              background-color: white;
              width: 24px;
              margin-top: 7px;
              display: block;
              transition: 0.3s ease-in-out;
            }

            &::before {
              content: "";
              height: 1px;
              background-color: white;
              width: 24px;
              display: block;
              transition: 0.3s ease-in-out;
              transform-origin: 1px;
            }

            &::after {
              content: "";
              height: 1px;
              background-color: white;
              width: 24px;
              margin-top: 7px;
              display: block;
              transition: 0.3s ease-in-out;
              transform-origin: 1px;
            }
        }

        &__burger-active {
          .line {
            opacity: 0;
          }

          &::before {
            rotate: 45deg;
          }

          &::after {
            rotate: -45deg;
          }
        }
      }

      &__menu {
        //display: none;
        opacity: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        box-sizing: border-box;
        padding-bottom: 40px;
        height: calc(100% - 90px);;
        //min-height: 570px;
        transition: 0.4s ease-out;
        .nav__second {
          max-height: 224px;
          //height: 100%;
          display: flex;
          flex-direction: column;
          padding-left: 16px;
          padding-right: 16px;
          padding-bottom: 12px;
          .nav__language-wrap {
            padding-top: 12px;
          }

          a {
            border-bottom: 1px solid var(--border-color);

            p {
              @include dmsans24();
              line-height: 40px;
              padding-top: 12px;
              padding-bottom: 12px;
              position: relative;
              width: fit-content;

              &::before {
                content: "";
                position: absolute;
                width: 0;
                height: 2px;
                background-color: #FFFFFF;
                bottom: 12px;
                left: 0;
                transition: 0.3s width;
              }
            }


          }

          .notactive {
            background: none;

            p {
              &::before {
                width: 100%;
              }
            }
          }

        }

        .nav__third {
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;
          padding-left: 16px;
          padding-right: 16px;

          h1 {}

          .nav__language-wrap {
            width: 100%;
          }

          .language_switcher {
            margin-top: min(24px, 1.7vh);
            margin-left: 0px;
          }
        }

        .nav__fourth {
          height: 56px;
          width: calc(100% - 32px);
          max-width: unset;
          margin: 8px 16px 8px 16px;
          box-sizing: border-box;
          background-color: var(--white-color);
          a{
            position: absolute;
          }
          p {
            color: var(--eerie_black-color);
          }
        }

        .nav__fifth {
          display: flex;
          border-top: 1px solid var(--border-color);
          padding: 0px 16px 0px 16px;
          justify-content: space-between;
          &__kontakt{
            padding-top: 24px;
            width: 50%;
            //border-right: 1px solid var(--border-color);
          }
          &__adress{
            padding-top: 24px;
            padding-left: 16px;
            width: 50%;
          }
          .nav__language-wrap {
            padding-bottom: 16px;
          }
        }
      }

      &__menu-active {
        transition: 1s ease-out;
        transition-delay: 1s;
        opacity: 100;
      }
    }

    .logo {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 152px;

      a {
        display: flex;
        justify-content: center;
      }

      img {
        width: 126px;
        height: 35px;
        cursor: pointer;
      }
    }

  }
}