.bigtext{
    //256px je o kolik se zmenÅ¡Ã­ box v pÅ™edchozÃ­ sekci
    height: calc(100vh - 200px - 256px);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: var(--white-color);
    margin-top: -1px;
    overflow: hidden;
    //box-shadow: 0 200px 0  var(--white-color);
    @include windowMax1440p{
        height: calc(100vh - 148px - 231px);
    }
    @include mobileMax952p{
        height: 320px;
        .bigtext_container{
            display: flex;
            justify-content: center;
        }
        .swap-wrapper{
            width: calc(100% - 45px);
        }
    }
    p{
        @include dmsans92();
        @include windowMax1440p {
            @include dmsans80();
        }
        @include mobileMax952p{
            font-size: 40px;
            line-height: 48px;
            br{
                display: none;
            }
        }
    }
    span{
        @include gradient();
    }
}