.smooth-wrapper{
    clip-path: inset(80px 0px 0px 0px);
    @include windowMax1440p{
        clip-path: inset(74px 0px 0px 0px);
    }
    @include mobileMax952p{
        clip-path: inset(80px 0px 0px 0px);
        transition: clip-path 1s;
    }
}

.smooth-wrapper-active{
    clip-path: inset(1em 0px 0px 0px);
    @include windowMax1440p{
        clip-path: inset(1em 0px 0px 0px);
    }
    @include mobileMax952p{
        clip-path: inset(80px 0px 0px 0px);
    }
}

.smooth-wrapper-nav-active{
    @include mobileMax952p{
        clip-path: inset(100vh 0px 0px 0px);
        transition: clip-path 1s;
    }
}

.swap-wrapper{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

body{
    font-size: 199px;
    @include windowMax1440p{
        font-size: 148px;
    }
}

#smooth-content{
    @include mobileMax952p{
        overflow: hidden !important;
    }
}

#smooth-wrapper{
    // height: 120% !important;
    @media (hover: none) { // touch device
      height: calc(var(--app-height)*1.2) !important;
    }
}