.video{
    margin-top: -1px;
    background-color: var(--white-color);
    padding-bottom: 200px;
    padding-top: 150px;
    overflow: hidden;
    @include mobileMax952p{
        padding-top: 0px;
        padding-bottom: 48px;
    }
    .container{
        aspect-ratio: 16 / 9;
        background-image: url('./images/bg/video_placeholder.webp');
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;
        margin-top: -1px;
        @include mobileMax952p{
            border-left: 16px solid var(--white-color);
            border-right: 16px solid var(--white-color);
        }
        img{
            width: 133px;
            height: 133px;
            @include windowMax1440p{
                width: 120px;
                height: 120px;
            }
            @include mobileMax952p{
                width: 52px;
                height: 52px;
            }
        }
        iframe{
            position: absolute;
            opacity: 0;
            transition: opacity 1.5s ease-in-out;
            width: 100%;
            height: 100%;
            box-sizing: border-box;
        }
        .video_square{
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            pointer-events: none;
            .video_circle{
                width: 0px;
                height: 0px;
                border-radius: 100%;
                background-color: transparent;
                box-shadow: 0 0 0 900px white;
            }
        }
    }
}