.service {
    height: 476px;
    width: 100%;
    overflow: hidden;
    -webkit-backface-visibility: hidden;
    /* add to fix webkit bug jitter */
    -webkit-transform: perspective(1000px);
    /* add to fix webkit bug jitter */
    margin-top: -1px;
    position: relative;
    @include mobileMax952p{
        height: fit-content;
        br{
            display: block;
        }
    }
    br{
        display: none;
    }
    &__content {
        height: 100%;
        width: 100%;
        transform: translateY(280px);
    }
    &__cover{
        position: absolute;
        height: 476px;
        width: 100%;
        background-color: white;
        z-index: 999;
        pointer-events: none;
    }
    .service-language-wrap {
        width: 120px;
        height: 28px;
        position: relative;
    }

    &__content__header {
        padding-bottom: 48px;
        height: 100px;
        z-index: 2;
        position: relative;
        @include windowMax1440p{
            padding-bottom: 40px;
            height: 100px;
            padding-top: 20px;
        }
        @include mobileMax952p{
            height: 56px;
            padding-bottom: 4px;
        }
        p {
            @include dmsans32_500();
            @include windowMax1440p{
                font-size: 28px;
                line-height: 40px;
            }
            @include mobileMax952p{
                font-size: 20px;
                line-height: 52px;
            }
        }

        span {
            @include dmsans32();
            @include gradient();
            @include windowMax1440p{
                font-size: 28px;
                line-height: 40px;
            }
            @include mobileMax952p{
                font-size: 20px;
                line-height: 52px;
            }
        }
    }
    .service_menu {
        margin-top: -1px;
        overflow: hidden;
        width: 100%;
        display: flex;
        justify-content: center;
        cursor: pointer;
        box-shadow: 0px 0px 0px 3000px var(--white-color);
        font-size: 64px;
        @include windowMax1440p{
            font-size: 48px;
        }
        @include mobileMax952p{
            flex-direction: column;
            box-sizing: border-box;
            max-width: 600px;
            padding: 0 16px;
            margin: 0 auto 0 auto;
            gap: 24px;
        }
        .service_box {
            width: 665px;
            height: 376px;
            position: relative;
            color: white;
            margin-top: -1px;
            transition: background-color .5s ease-out, color .5s ease-out;
            font-size: 1em;
            display: flex;
            align-items: center;

            //justify-content: center;
            @include windowMax1440p {
                width: 550px;
                height: 305px;
            }
            @include mobileMax952p{
                width: 100%;
                box-sizing: border-box;
                padding: 28px;
                height: unset;
                aspect-ratio: 340 / 190;
                box-shadow: 0px 0px 0px 24px var(--white-color);
            }

            path {
                fill: url(#paint0_linear_228_306);
            }

            circle {
                fill: white;
            }

            .content-wrap {
                padding: 1em;
                padding-top: 3em;
                position: absolute;
                pointer-events: none; 
                @include mobileMax952p{
                    padding: 0px;
                    height: calc(100% - 56px);
                    position: relative;
                    display: flex;
                    justify-content: center;
                    flex-direction: column;
                }
                .service_text {
                    transform-origin: 0;
                    font-size: 48px;  
                    @include windowMax1440p {
                        font-size: 40px;
                    }
                    @include mobileMax952p{
                        font-size: 32px;
                        line-height: 40px;
                        width: 210px;
                    }

                    p {
                        @include dmsans48_500();
                        font-size: 1em;
                        @include mobileMax952p{
                            line-height: 40px;
                        }
                    }

                    span {
                        @include dmsans48();
                        font-size: 1em;
                        @include mobileMax952p{
                            line-height: 40px;
                        }
                    }
                }

                .show_more {
                    padding-top: 42px;
                    display: flex;
                    align-items: center;

                    @include windowMax1440p {
                        padding-top: 24px;
                    }
                    @include mobileMax952p {
                        padding-top: 24px;
                    }

                    svg {
                        width: 32px;
                        height: 32px;
                        @include mobileMax952p{
                            width: 24px;
                            height: 24px;
                        }
                    }

                    p {
                        @include dmsans16();
                        display: inline;
                        padding-left: 16px;
                        top: 50%;
                        transform: translateY(-50%);

                        @include windowMax1440p {
                            @include dmsans14();
                        }
                    }

                }
            }
        }

        .service_header {
            display: inline;
        }

        .right {
            box-shadow: 500px 0 0 500px var(--white-color);
            margin-right: -1px;

            .content-wrap {
                padding-left: 64px;

                @include windowMax1440p {
                    padding-left: 48px;
                }
            }
        }

        .left {
            box-shadow: -500px 0 0 500px var(--white-color);

            .content-wrap {

                //right: 205px;
                @include windowMax1440p {
                    //right: 178px;
                }
            }
        }
    }
}

.notactive {
    background: #ffffffe0;

    .content-wrap {
        color: var(--black-color);

        .show_more {
            svg {
                path {
                    fill: white;
                }

                circle {
                    fill: url(#paint0_linear_228_306);
                }
            }
        }
    }
}