.footerinfo{
    position:relative;
    //min-height: 680px;
    background-image: url("./images/bg/footer_dark.webp");
    background-position: center;
    background-size: cover;
    color: var(--cultured_white-color);
    overflow: hidden;
    .footerinfo-wrap{
        padding-top: 215px;
        @include mobileMax952p{
            padding-top: 50px;
        }
    }
    .logo{
        height: 78px;
        border-bottom: 1px solid var(--border-color);
        display: flex;
        align-items: center;
        @include windowMax1440p{
            height: 74px;
        }
        @include mobileMax952p{
            height: 94px;
        }
        img{
            padding-left: 64px;
            height: 35px;
            @include windowMax1440p{
                padding-left: 48px;
                padding-top: 0px;
            }
            @include mobileMax952p{
                padding-left: 16px;
                padding-top: 0px;
            }
        }
    }
    .container{
        height: 441px;
        display: flex;
        border-left: 1px solid var(--border-color);
        border-right: 1px solid var(--border-color);
        @include mobileMax952p{
            flex-direction: column;
            border-left: 0px solid var(--border-color);
            border-right: 0px solid var(--border-color);
            gap: 60px;
            height: fit-content;
        }
        a{
            position: relative;
            display: table;
            &:link, &:visited,&:active{
                text-decoration: none;
                color: var(--cultured_white-color);
            }
              
            &:hover{
                &::before{
                    width: 100%;
                }
            }

            &::before{
                content: "";
                position: absolute;
                width: 0;
                height: 2px;
                background-color: #FFFFFF;
                bottom: -2px;
                left: 0;
                transition: 0.3s width;
            }
        }

        .notactive{
            background:none;
            &::before{
                width: 100%;
            }
        }

        h1{
            @include dmsans24();
            padding-bottom: 32px;
            padding-top: 152px;
            @include windowMax1440p{
                font-size: 22px;
                line-height: 29px;
                padding-bottom: 24px;
            }
            @include mobileMax952p{
                font-size: 20px;
                line-height: 26px;
                padding-top: 0px;
                //padding-bottom: 24px;
            }
        }
        p{
            @include dmsans20();
            @include windowMax1440p{
                font-size: 18px;
                line-height: 30px;
            }
            @include mobileMax952p{
                font-size: 16px;
                line-height: 32px;
            }
        }
        .left{
            padding-left: 54px;
            @include mobileMax952p{
                padding-left: 0px;
            }
        }
        .right{
            padding-left: 130px;
            @include windowMax1440p{
                padding-left: 65px;
            }
            @include mobileMax952p{
                padding-left: 0px;
            }
        }
        .fifty{
            flex: 0 0 50%;
            display: flex;
            @include mobileMax952p{
                flex: unset;
                flex-direction: column;
                align-items: flex-start;
                gap: 60px;
            }
        }
        .first {
            border-right: 1px solid var(--border-color);
            @include mobileMax952p{
                padding-top: 60px;
            }
            .text{
                padding-left: 111px;
                @include mobileMax952p{
                    padding-left: 0px;
                }
            }
            @include mobileMax952p{
                border-right: 0px solid var(--border-color);
            }
        }
    }

    .copyright{
        height: 79px;
        border-top: 1px solid var(--border-color);
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include windowMax1440p{
            height: 66px;
        }
        @include mobileMax952p{
            height: 118px;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 16px;
            margin-top: 60px;
        }
        p{
            padding: 0px 63px 0px 63px;
            @include dmsans14();
            @include windowMax1440p{
                padding: 0px 48px 0px 48px;
            }
            @include mobileMax952p{
                padding: 0px 16px 0px 16px;
            }
        }
    }
}