.benefits{
    padding-bottom: 200px;
    overflow: hidden;
    -webkit-backface-visibility: hidden; /* add to fix webkit bug jitter */
    -webkit-transform: perspective(1000px); /* add to fix webkit bug jitter */
    margin-top: -1px;
    @include mobileMax952p{
        padding-bottom: 56px;
        
    }
    @media (hover: none) {
        background-color: var(--white-color);
    }

    .swap-wrapper-benefits{
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: end;
        @include mobileMax952p{
            align-items: center;
        }
    }
    .container{
        display: flex;
        flex-wrap:wrap;
        box-shadow: 0px 0px 0px 3000px var(--white-color);
        .box{
            width: 50%;
            height: 376px;
            background-color: var(--white-color);
            border: solid 1px #DFDFDF;
            transition: .4s ease-out;
            box-sizing: border-box;
            
            @include mobileMax952p{
                /*
                &::after{
                    content: "";
                    background-color: var(--white-color);
                    position: absolute;
                    right: -1px;
                    height: 100%;
                    width: 1px;
                    top: 0px;
                }*/
                width: 100%;
                height: unset;
                aspect-ratio: 340 /200  ;
                box-shadow: 16px 0px 0px 0px var(--white-color),-16px 0px 0px 0px var(--white-color);
                position: relative;
            }
            h1{
                @include dmsans64_500();
                @include windowMax1440p{
                    @include dmsans56_500();
                }
                @include mobileMax952p{
                    font-size: 40px;
                    line-height: 52px;
                }
            }
            .text{
                height: 100%;
                span{
                    @include dmsans32_500();
                    @include windowMax1440p{
                        @include dmsans24_500();
                        line-height: 40px;
                    }
                    @include mobileMax952p{
                        font-size: 20px;
                        line-height: 32px;
                    }
                }
                p{
                    @include dmsans32();
                    @include windowMax1440p{
                        @include dmsans24();
                        line-height: 40px;
                    }
                    @include mobileMax952p{
                        font-size: 20px;
                        line-height: 32px;
                    }
                }
            }
            .wrap{
                padding: 64px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                height: calc(100% - 128px);
                @include mobileMax952p{
                    padding: 24px;
                    height: 100%;
                    box-sizing: border-box;
                }
            }
            
        }
        .first,.third,.fifth{
            border-right: solid 1px var(--white-color);
            @include mobileMax952p{
                border-right: solid 1px #DFDFDF; 
            }
        }
        .third,.fourth,.fifth,.sixth{
            border-top: solid 1px var(--white-color);
        }

        .first{
            border-top:solid 1px var(--white-color);
            border-left:solid 1px var(--white-color);
            @include mobileMax952p{
                height: 104px;
                border: solid 0px white;
                &::after{
                    content: "";
                    background-color: var(--white-color);
                    position: absolute;
                    right: -1px;
                    height: 100%;
                    width: 1px;
                    top: 0px;
                }
                &::before{
                    content: "";
                    background-color: var(--white-color);
                    position: absolute;
                    left: -1px;
                    height: 100%;
                    width: 1px;
                    top: 0px;
                }
            }
            p{
                @include dmsans64_500();
                @include windowMax1440p{
                    @include dmsans56_500();
                }
                @include mobileMax952p{
                    font-size: 24px;
                    line-height: 36px;
                }
            }
            span{
                @include dmsans64();
                @include windowMax1440p{
                    @include dmsans56();
                }
                @include mobileMax952p{
                    font-size: 24px;
                    line-height: 36px;
                }
                @include gradient();
            }
        }
        .sixth{
            border-right:solid 1px var(--white-color);
            border-bottom:solid 1px var(--white-color);
            @include mobileMax952p{
                display: none;
            }
        }
        .ad-one{
            border-top:solid 1px var(--white-color);
        }
        .ad-two{
            border-top:solid 1px var(--white-color);
            border-right:solid 1px var(--white-color);
            @include mobileMax952p{
                border-right: solid 1px #DFDFDF;
            }
        }

        .hidden{
            display: none;
        }

        .second,.third,.fourth,.fifth,.ad-one,.ad-two{
            background: linear-gradient(to left, var(--white-color) 50%, transparent 50%) right;
            background-size: 200%;
            h1{
                span{
                    @include gradient2();
                }
            }
            .text{
                max-width: 526px;
            }
        }
        @media (hover: hover) {
            .second:hover,.third:hover,.fourth:hover,.fifth:hover,.ad-one:hover,.ad-two:hover{
                background-position: left;
                color:var(--white-color);
                h2{
                    color:var(--white-color);
                }
                span{
                    -webkit-text-fill-color: unset;
                }
            } 
        }
    }
}