.quote{
    height: 100vh;
    background-color: transparent;
    overflow: hidden;
    @include mobileMax952p{
        height: 516px;
    }
    .container{
        display: flex;
        color: white;
        border: 0 1px 0 1px solid var(--border-color);
        @include mobileMax952p{
            flex-direction: column;
            height: 100%;
            justify-content: space-between;
        }
        .fifty{
            width: 50%;
            @include mobileMax952p{
                width: 100%;
                br{
                    display: none;
                }
            }
        }
        p{
            box-sizing: border-box;
            max-width: 665px;
            @include windowMax1440p{
                max-width: 550px;
            }
            @include mobileMax952p{
                max-width: 600px;
            }
        }
        .left{
            padding-top: 200px;
            @include mobileMax952p{
                padding-top: 32px;
                height: 144px;
            }
            .text{
                padding-left: 91px;
                @include mobileMax952p{
                    padding-left: 0px;   
                }
                p{
                    @include dmsans92_500();
                    @include windowMax1440p{
                        @include dmsans80_500();
                    }
                    @include mobileMax952p{
                        font-size: 40px;
                        line-height: 48px;
                        max-width: 205px;
                    }

                }
                span{
                    @include dmsans92();
                    @include windowMax1440p{
                        @include dmsans80();
                    }
                    @include mobileMax952p{
                        font-size: 40px;
                        line-height: 48px;
                    }
                }
            }
        }
        .right{
            min-height: 100vh;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding-bottom: 152px;
            box-sizing: border-box;
            border-left: 1px solid var(--border-color);
            @include mobileMax952p{
                border-left: unset;
                min-height: unset;
                padding-bottom: 32px;
                box-sizing: content-box;
                height: 192px;
            }
            p{
                padding-left: 102px;
                padding-right: 56px;
                @include dmsans28();
                line-height: 48px;
                @include windowMax1440p{
                    @include dmsans20();
                    line-height: 40px;
                    max-width: 420px;
                }
                @include mobileMax952p{
                    padding-left: 0px;
                    line-height: 32px;
                    font-size: 16px;
                }
            }
        }
    }
}