.reasons {
    background-color: var(--white-color);
    overflow: hidden;
    //min-height: 1319px;
    //padding-bottom: 200px;
    .hidden-size-meter{
        opacity: 0;
        display: none;
        @include mobileMax952p{
            display: block;
        }
    }
    .swap-wrapper-reasons {
        width: 100%;
        position: relative;
    }

    .headline-content {
        height: 248px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
        @include windowMax1440p {
            height: 224px;
        }
        @include mobileMax952p{
            height: unset;
        }
    }

    .container {
        padding-top: 200px;

        @include windowMax1440p {
            padding-top: 180px;
        }
        @include mobileMax952p {
            padding-top: 64px;
        }

        .headline {
            height: 544px;
            @include windowMax1440p {
                height: 416px;
            }
            @include mobileMax952p{
                height: unset;
                padding-bottom: 56px;
            }

            h1 {
                @include dmsans92();
                padding-bottom: 48px;

                @include windowMax1440p {
                    @include dmsans80();
                }
                @include mobileMax952p{
                    font-size: 40px;
                    line-height: 48px;
                    top: 0;
                    padding-bottom: 32px;
                    //width: 248px;
                }
            }

            p {
                @include dmsans28();
                padding-bottom: 200px;
                color: #6B727A;

                @include windowMax1440p {
                    font-size: 20px;
                    line-height: 40px;
                }
                @include mobileMax952p{
                    font-size: 20px;
                    line-height: 32px;
                    padding-bottom: 0px;
                    top: 0;
                }
            }

            span {
                @include gradient();
            }
        }

        .bulletpoints {
            width: 100%;
            padding-bottom: 1px;

            p {
                transition: 0.3s ease-out;
                @include dmsans32();
                max-width: 1330px;

                @include windowMax1440p {
                    font-size: 24px;
                    line-height: 40px;
                }
                @include mobileMax952p{
                    font-size: 16px;
                    line-height: 24px;
                }
            }

            .row {
                min-height: 112px;
                width: 100%;
                border-top: 1px solid #DFDFDF;
                display: flex;
                align-items: center;
                position: relative;
                @include windowMax1440p{
                    min-height: 106px;
                }
                @include mobileMax952p{
                    p{
                        padding-right: 12px;
                    }
                }

                .dot {
                    transition: 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940);
                    width: 21px;
                    height: 21px;
                    border-radius: 100%;
                    background: linear-gradient(90deg, var(--brown-color), var(--bronze-color));
                    opacity: 0;
                    position: absolute;
                    @include mobileMax952p{
                        width: 16px;
                        height: 16px;
                    }
                }
            }
            @media (hover: hover) {
                .row:hover {
                    p {
                        transform: translateX(54px);
                    }

                    .dot {
                        opacity: 100;
                    }
                    @include mobileMax952p{
                        p{
                            transform: translateX(24px);
                        }
                    }
                }
            }

            .last {
                border-bottom: 1px solid #DFDFDF;
            }

            .lastlast {
                border-top: 0px;
                border-bottom: 1px solid #DFDFDF;
            }

            .lastlast.untoggled_service {
                display: none;
            }
        }
    }
}